"use client";
import { useRouter } from "next/navigation";
import { Typography, Box, Button } from "@mui/material";
import { useState } from "react";
import CustomForm from "@/app/components/CustomForm";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import SaveIcon from "@mui/icons-material/Save";
import { motion } from "framer-motion";
import { VEHICLE_FORM_FIELDS } from "@/app/components/vehicle-master/vehicleMasterFormFields";

const AddUser = () => {
  const router = useRouter();
  const [form, setForm] = useState(
    VEHICLE_FORM_FIELDS.reduce((acc, tab) => {
      tab.sections.forEach((section) => {
        section.fields.forEach((field) => {
          acc[field.key] = field.type === "multiselect" ? [] : "";
        });
      });
      return acc;
    }, {})
  );

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSave = () => {
    console.log("Form Data:", form);
  };

  const handleBack = () => {
    router.back();
  };

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.9, x: -20 }}
      animate={{ opacity: 1, scale: 1, x: 0 }}
      exit={{ opacity: 0, scale: 0.9, x: 20 }}
      transition={{ duration: 1, ease: [0.4, 0, 0.2, 1] }}
      style={{
        height: "95vh", // full viewport height
        overflowY: "auto",
        display: "flex",
        flexDirection: "column",
      }}
    >
      {/* Sticky Header */}
      <Box
        sx={{
          flexShrink: 0,
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Box>
          <Typography variant="h6" sx={{ fontWeight: 700 }}>
            Add Vehicle
          </Typography>
          <Typography variant="body2" sx={{ color: "#666" }}>
            Fill in the details below to add a new vehicle.
          </Typography>
        </Box>

        <Box>
          <Button
            className="btn-primary"
            sx={{ textTransform: "none", marginRight: 1 }}
            startIcon={<SaveIcon />}
            onClick={handleSave}
          >
            Save
          </Button>
          <Button
            className="btn-secondary"
            sx={{ textTransform: "none" }}
            startIcon={<ArrowBackIcon />}
            onClick={handleBack}
          >
            Back
          </Button>

        </Box>
      </Box>

      {/* Scrollable Form */}
      <CustomForm
        formSchema={VEHICLE_FORM_FIELDS}
        formData={form}
        onChange={handleChange}
      />
    </motion.div>
  );
};

export default AddUser;
